* {
    margin: 0;
    padding: 0;
}
body {
    margin: 0;
    padding: 0;
}
footer {
    width: 100%;
    text-align: center;
    border-top: 1px solid #6f7173;
    height: 80px;
    display: grid;
    align-items: center;
}
hr {
    width: 100%;
}
.wrap {
    display: grid;
    justify-items: center;
}
h1 {
    font-family: "Segoe Light", serif;
    font-size: 42px;
    color: #fff;
    font-weight: 100;
}
h2 {
    font-family: "Segoe Regular", serif;
    font-size: 26px;
    color: #fff;
    font-weight: 100;
}
h3 {
    font-family: "Segoe Light", serif;
    font-size: 42px;
    color: #6f7173;
}
h4 {
    font-family: "Segoe Light", serif;
    font-size: 26px;
    color: #6f7173;
}
