.container {
    min-width: 1000px;
    max-width: 1000px;
}
.sign_up_btn {
    font-family: "Segoe Regular", serif;
    font-size: 22px;
    background: none;
    border: 1px solid #fff;
    align-items: center;
    color: #fff;
    display: flex;
    justify-items: center;
    padding: 5px 10px;
    border-radius: 3px;
    transition: .2s ease-out;
    margin-top: 40px;
}
.sign_up_btn img {
    margin: 0 10px 0 0;
}
.sign_up_btn:hover {
    transition: .2s ease-in;
    background: #5094f2;
    border: 1px solid #5094f2;
}
@media screen and (min-width: 0) and (max-width: 650px) {
    .container {
        min-width: 100%;
        max-width: 100%;
    }
}
@font-face {
    font-family: "Segoe Light";
    src: url(/landing-page-2/static/media/SegoeLight.cbeb4742.ttf) format("truetype");
}
@font-face {
    font-family: "Segoe Regular";
    src: url(/landing-page-2/static/media/SegoeRegular.fc15cc81.ttf) format("truetype");
}
header {
  max-width: 1250px;
  min-width: 1250px;
  height: 90px;
  display: grid;
  justify-items: center;
  align-items: center; }
  header .container {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    header .container .menu .menu__ul {
      display: flex; }
      header .container .menu .menu__ul .menu__ul_li {
        list-style: none;
        padding: 30px 0;
        margin: 0 15px;
        border-bottom: 2px solid transparent;
        transition: .2s ease-out; }
        header .container .menu .menu__ul .menu__ul_li:hover {
          transition: .2s ease-in;
          border-bottom: 2px solid #5094f2; }
        header .container .menu .menu__ul .menu__ul_li a {
          text-decoration: none;
          font-size: 20.5px;
          color: #6c6c6c;
          font-family: "Segoe Regular", serif; }

@media screen and (min-width: 0) and (max-width: 650px) {
  header {
    min-width: 100%;
    max-width: 100%; }
    header .container {
      justify-content: center; }
      header .container .menu {
        display: none; } }

.offers {
  height: 400px;
  display: grid;
  align-items: center; }
  .offers .container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; }
    .offers .container .offers__item {
      display: grid;
      justify-items: center;
      text-align: center; }
      .offers .container .offers__item .circle {
        width: 186px;
        height: 186px;
        border-radius: 50%;
        display: grid;
        justify-items: center;
        align-items: center;
        transition: .4s ease-out;
        transform: rotate3d(0, 1, 0, 0deg); }
        .offers .container .offers__item .circle:hover {
          transition: .4s ease-in;
          transform: rotate3d(0, 1, 0, 180deg); }
      .offers .container .offers__item .circle_blue {
        background: #5094f2; }
      .offers .container .offers__item .circle_red {
        background: #d94748; }
      .offers .container .offers__item .circle_yellow {
        background: #ebbb11; }
      .offers .container .offers__item h5 {
        margin-top: 28px; }
      .offers .container .offers__item p {
        font-family: "Segoe Regular", serif;
        padding: 0 25px; }

@media screen and (min-width: 0) and (max-width: 650px) {
  .offers {
    height: auto;
    padding: 15px 0; }
    .offers .container {
      grid-template-columns: 1fr; }
      .offers .container .offers__item .circle_red {
        margin-top: 20px; }
      .offers .container .offers__item .circle_yellow {
        margin-top: 20px; } }

.cart {
  text-align: left;
  margin-bottom: 60px; }
  .cart .container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center; }
    .cart .container .cart__text {
      grid-area: cart; }
      .cart .container .cart__text ul li {
        font-family: "Segoe Regular", serif;
        font-size: 16px;
        color: #909090;
        list-style: none;
        margin: 12px 0; }
        .cart .container .cart__text ul li:first-child {
          margin-top: 30px; }
    .cart .container .cart__img {
      grid-area: img; }

@media screen and (min-width: 0) and (max-width: 650px) {
  .cart {
    text-align: center; }
    .cart .container {
      grid-template-columns: 1fr;
      grid-template-areas: "cart" "img"; }
      .cart .container .cart__img {
        margin-top: 25px; }
      .cart .container .cart__text {
        margin-top: 25px; } }

.slider {
  width: 100%;
  height: 600px;
  background: url(/landing-page-2/static/media/dsfsdfsdfds@3x.eeb911b6.png) no-repeat;
  background-size: cover;
  overflow: hidden;
  position: relative; }
  .slider .slider__container {
    height: 100%;
    min-width: 100%;
    max-width: 100%;
    display: flex;
    z-index: 2;
    position: relative; }
    .slider .slider__container .slider__container__item {
      min-width: 100%;
